import { verifyTypedData, type Address } from 'viem';
import { base } from 'viem/chains';

export interface EIP712Domain {
  name: string;
  version: string;
  chainId: number;
  verifyingContract: Address;
}

export interface PaymentAuthorization {
  from: Address;
  to: Address;
  value: string;
  validAfter: string;
  validBefore: string;
  nonce: string;
}

/**
 * Verify EIP-712 signature for x402 payment authorization
 * Uses EIP-3009 TransferWithAuthorization domain for USDC
 * This provides an additional security layer before CDP verification
 */
export async function verifyPaymentSignature(
  signature: string,
  authorization: PaymentAuthorization,
  signerAddress: Address,
  chainId: number = 8453, // Base mainnet
  asset?: string // Token contract address (optional, defaults to USDC)
): Promise<boolean> {
  try {
    // EIP-3009 domain for USDC on Base (per official @x402/evm SDK)
    // The domain comes from the token contract itself
    const domain: EIP712Domain = {
      name: 'USD Coin',
      version: '2',
      chainId,
      verifyingContract: (asset || '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913') as Address, // Base USDC
    };

    // EIP-3009 TransferWithAuthorization types
    const types = {
      TransferWithAuthorization: [
        { name: 'from', type: 'address' },
        { name: 'to', type: 'address' },
        { name: 'value', type: 'uint256' },
        { name: 'validAfter', type: 'uint256' },
        { name: 'validBefore', type: 'uint256' },
        { name: 'nonce', type: 'bytes32' },
      ],
    };

    // Verify the signature
    const isValid = await verifyTypedData({
      address: signerAddress,
      domain,
      types,
      primaryType: 'TransferWithAuthorization',
      message: authorization as Record<string, unknown>,
      signature: signature as `0x${string}`,
    });

    return isValid;
  } catch (error) {
    console.error('[SignatureVerifier] Verification error:', error);
    return false;
  }
}

/**
 * Validate payment authorization fields
 */
export function validatePaymentAuthorization(
  authorization: PaymentAuthorization
): { valid: boolean; error?: string } {
  const now = Math.floor(Date.now() / 1000);
  const validAfter = parseInt(authorization.validAfter);
  const validBefore = parseInt(authorization.validBefore);

  if (now < validAfter) {
    return { valid: false, error: 'Payment not yet valid' };
  }

  if (now >= validBefore) {
    return { valid: false, error: 'Payment has expired' };
  }

  if (!authorization.from || !authorization.to) {
    return { valid: false, error: 'Missing from or to address' };
  }

  if (!authorization.value || BigInt(authorization.value) <= BigInt(0)) {
    return { valid: false, error: 'Invalid payment amount' };
  }

  if (!authorization.nonce) {
    return { valid: false, error: 'Missing nonce' };
  }

  return { valid: true };
}

