// =============================================================================
// CDP/x402 Client Wrapper
// =============================================================================
// Wraps all calls to Coinbase/CDP/x402 with normalized error handling
// and structured logging

import { createLogger, type LogContext } from './logger';

const logger = createLogger({ component: 'CDPClient' });

/**
 * Normalized CDP error codes
 */
export enum CDPErrorCode {
  CDP_TIMEOUT = 'CDP_TIMEOUT',
  CDP_4XX = 'CDP_4XX',
  CDP_5XX = 'CDP_5XX',
  CDP_INVALID_RESPONSE = 'CDP_INVALID_RESPONSE',
  CDP_NETWORK_ERROR = 'CDP_NETWORK_ERROR',
  CDP_AUTH_ERROR = 'CDP_AUTH_ERROR',
  CDP_RATE_LIMIT = 'CDP_RATE_LIMIT',
  CDP_UNKNOWN_ERROR = 'CDP_UNKNOWN_ERROR',
}

/**
 * Normalized CDP error
 */
export class CDPError extends Error {
  constructor(
    public code: CDPErrorCode,
    message: string,
    public statusCode?: number,
    public cdpErrorId?: string,
    public responseBody?: any
  ) {
    super(message);
    this.name = 'CDPError';
    // Ensure stack trace is captured
    if (Error.captureStackTrace) {
      Error.captureStackTrace(this, CDPError);
    }
  }
}

/**
 * Normalize HTTP status code to CDP error code
 */
function normalizeErrorCode(statusCode: number | undefined): CDPErrorCode {
  if (!statusCode) {
    return CDPErrorCode.CDP_UNKNOWN_ERROR;
  }
  
  if (statusCode >= 500) {
    return CDPErrorCode.CDP_5XX;
  }
  
  if (statusCode === 401 || statusCode === 403) {
    return CDPErrorCode.CDP_AUTH_ERROR;
  }
  
  if (statusCode === 429) {
    return CDPErrorCode.CDP_RATE_LIMIT;
  }
  
  if (statusCode >= 400) {
    return CDPErrorCode.CDP_4XX;
  }
  
  return CDPErrorCode.CDP_UNKNOWN_ERROR;
}

/**
 * Extract safe excerpt from response body for logging
 * Removes sensitive data and truncates long responses
 */
function extractSafeResponseExcerpt(body: any, maxLength: number = 500): string {
  if (!body) return '';
  
  try {
    const str = typeof body === 'string' ? body : JSON.stringify(body);
    if (str.length <= maxLength) {
      return str;
    }
    return str.substring(0, maxLength) + '... [truncated]';
  } catch {
    return '[unable to serialize response]';
  }
}

/**
 * Wrapper for CDP API calls with error handling and logging
 */
export async function callCDP<T>(
  operation: string,
  requestFn: () => Promise<Response>,
  context: {
    requestId?: string;
    apiKeyId?: string;
    endpointId?: string;
    targetUrl?: string;
  }
): Promise<T> {
  const startTime = Date.now();
  const logContext: LogContext = {
    requestId: context.requestId,
    apiKeyId: context.apiKeyId,
    endpoint: context.endpointId,
    cdpStatus: 'pending',
  };
  
  try {
    logger.debug({ ...logContext, operation, targetUrl: context.targetUrl }, `CDP call: ${operation}`);
    
    const response = await requestFn();
    const duration = Date.now() - startTime;
    
    // Extract response body safely
    let responseBody: any = null;
    let responseText = '';
    try {
      responseText = await response.text();
      if (responseText) {
        responseBody = JSON.parse(responseText);
      }
    } catch {
      // Response might not be JSON, that's okay
      responseBody = responseText;
    }
    
    // Check if response is successful
    if (!response.ok) {
      const errorCode = normalizeErrorCode(response.status);
      const cdpErrorId = responseBody?.error?.id || responseBody?.errorId || response.headers.get('x-request-id');
      const errorMessage = responseBody?.error?.message || responseBody?.message || `CDP API error: ${response.statusText}`;
      
      const error = new CDPError(
        errorCode,
        errorMessage,
        response.status,
        cdpErrorId,
        responseBody
      );
      
      // Log structured error
      logger.error({
        ...logContext,
        operation,
        targetUrl: context.targetUrl,
        errorCode: error.code,
        cdpErrorId: error.cdpErrorId,
        statusCode: response.status,
        durationMs: duration,
        responseExcerpt: extractSafeResponseExcerpt(responseBody),
      }, `CDP call failed: ${operation}`);
      
      throw error;
    }
    
    // Log successful call
    logger.info({
      ...logContext,
      operation,
      targetUrl: context.targetUrl,
      cdpStatus: 'success',
      statusCode: response.status,
      durationMs: duration,
    }, `CDP call succeeded: ${operation}`);
    
    return responseBody as T;
  } catch (error) {
    const duration = Date.now() - startTime;
    
    // Handle network errors
    if (error instanceof TypeError && error.message.includes('fetch')) {
      const cdpError = new CDPError(
        CDPErrorCode.CDP_NETWORK_ERROR,
        `Network error calling CDP: ${error.message}`,
        undefined,
        undefined,
        undefined
      );
      
      logger.error({
        ...logContext,
        operation,
        targetUrl: context.targetUrl,
        errorCode: cdpError.code,
        durationMs: duration,
        error: error,
      }, `CDP network error: ${operation}`);
      
      throw cdpError;
    }
    
    // Handle timeout errors
    if (error instanceof Error && (error.message.includes('timeout') || error.message.includes('aborted'))) {
      const cdpError = new CDPError(
        CDPErrorCode.CDP_TIMEOUT,
        `CDP call timed out: ${operation}`,
        undefined,
        undefined,
        undefined
      );
      
      logger.error({
        ...logContext,
        operation,
        targetUrl: context.targetUrl,
        errorCode: cdpError.code,
        durationMs: duration,
        error: error,
      }, `CDP timeout: ${operation}`);
      
      throw cdpError;
    }
    
    // Re-throw CDPError as-is
    if (error instanceof CDPError) {
      throw error;
    }
    
    // Wrap unknown errors
    const cdpError = new CDPError(
      CDPErrorCode.CDP_UNKNOWN_ERROR,
      `Unknown error calling CDP: ${error instanceof Error ? error.message : String(error)}`,
      undefined,
      undefined,
      undefined
    );
    
    logger.error({
      ...logContext,
      operation,
      targetUrl: context.targetUrl,
      errorCode: cdpError.code,
      durationMs: duration,
      error: error,
    }, `CDP unknown error: ${operation}`);
    
    throw cdpError;
  }
}

/**
 * Helper to add request ID to CDP API calls
 */
export function addRequestIdToCDPHeaders(
  headers: HeadersInit,
  requestId?: string
): HeadersInit {
  const headersObj = headers instanceof Headers ? Object.fromEntries(headers.entries()) : 
                     Array.isArray(headers) ? Object.fromEntries(headers) : 
                     headers;
  
  if (requestId) {
    return {
      ...headersObj,
      'x-request-id': requestId,
    };
  }
  
  return headersObj;
}







